    font-size: 14px;
    line-height: 1.42857;
    outline: none;
    padding: 6px 12px;
    -webkit-transition: border 0.3s ease-in-out, background-color 0.3s ease-in-out;
    transition: border 0.3s ease-in-out, background-color 0.3s ease-in-out;
    white-space: nowrap; }
    .title-box > button:hover {
