.FancyTabs-tablist {
  list-style-type: none;
  margin: 0;
  padding: 0; }
  .FancyTabs-tablistItem {
    display: inline-block; }
    @media screen and (min-width: 750px) {
      .FancyTabs-tablistItem + .FancyTabs-tablistItem {
        margin-left: 0.3em; } }

.FancyTabs-panel {
  border-top: 5px solid #2697d1;
  padding: 1em;
  position: relative;
  z-index: 2;
  background: rgba(0, 0, 0, 0.025);
  overflow: hidden; }

.FancyTabs-tab {
  cursor: pointer; }
  .FancyTabs-tab:focus {
    outline: 0; }
  .FancyTabs-tabInner {
    border: 1px solid transparent;
    border-bottom: 0;
    position: relative;
    padding: 15px 20px;
    background: #f1f1f1;
    position: relative;
    transition: transform 0.2s ease-out, background 0.2s linear;
    -webkit-transition: -webkit-transform 0.2s ease-out, background 0.2s linear; }
    @media screen and (min-width: 750px) {
      .FancyTabs-tabInner {
        transform: translateY(5px);
        -webkit-transform: translateY(5px); } }
    .FancyTabs-tabInner.is-active {
      background: #2697d1;
      border-color: #41d0cb;
      border-width: 1px;
      color: #fff;
      text-decoration: none; }
      @media screen and (min-width: 750px) {
        .FancyTabs-tabInner.is-active {
          transform: translateY(0);
          -webkit-transform: translateY(0); }
          .FancyTabs-tabInner.is-active::after {
            content: "";
            background: #fff;
            height: 1px;
            position: absolute;
            bottom: -1px;
            left: 0;
            right: 0; } }

@media screen and (min-width: 750px) {
  .FancyTabs-panelInner {
    transition: transform 0.3s ease; }
    .FancyTabs-panelInner.is-enter {
      transform: translateY(-120%); }
    .FancyTabs-panelInner.is-enter-active, .FancyTabs-panelInner.is-leave {
      transform: translateY(0); }
    .FancyTabs-panelInner.is-leave-active {
      transform: translateY(120%); } }

@media screen and (max-width: 750px) {
  .FancyTabs-tablist {
    margin: 0 auto;
    width: 70%; }
    .FancyTabs-tablistItem {
      display: block; }
  .FancyTabs-tabInner {
    padding: 1.5% 1%; } }
